.nbp-progress-container {
    background-color: @dark_white;
    margin-left: 0px;
    margin-right: 0px;
    //modificareply
    z-index: 9040 !important;
    border-bottom: 1px solid rgba(204, 204, 204, .5);
    //border-bottom: 1px solid @light_gray;
    //border-top: 1px solid @light_gray;
}

.nbp-progress-container.sticky {
    position: fixed !important;
    //modificareply
    top: 42px;
    //top:112px;
    left:0px;
    width:100% !important;
    z-index: 9040 !important;
    -moz-transition: all 0.25s ease-out;
    -o-transition: all 0.25s ease-out;
    transition: all 0.25s ease-out;
}
:host( .sticky.menu-opened ){
  .nbp-progress-container.sticky{
    
    top: calc( ~"112px + 22em" )!important;
    
  }
}

.progress-container {
    display: table;
}

.progress-steps-container {
    height: 5.000em;
    display: table-cell;
    padding-top: 5px;
    padding-bottom: 5px;
    vertical-align: middle;
    text-align: center;
    >.step {
      display:inline-block;
      float:none !important;
      &.step-grande >.progress {
        width: 6.875em;
      }
      &.step-medio >.progress {
        width: 5em;
      }
      &.step-piccolo >.progress {
        width: 2.5em;
      }
    }
}

.progress {
    //modificareply
    height: 0.625em;
    //height: 0.875em;
    // background-color: @lightest_gray;
    //modificareply
    background-color: transparent;
    border-radius: 0.625em;
    border: 0.125em solid @light_gray;
    margin-bottom: 0.250em;
    box-shadow: none;
    overflow: hidden;
}

.progress.active {
    border: 0.125em solid @green;
}

.progress-bar {
    border-radius: 0.625em;
    background-color: @green;
    box-shadow: none;
}

.progress-step-name {
    position: relative;
    text-align: center;
    font-size: 0.625em;
    font-weight: bold;
    text-transform: uppercase;
    color: @gray;
}

.progress-step-name.arrowStep:after {
    position: absolute;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    content: " ";
    width: 15px;
    height: 15px;
    border: 0 solid @light_gray;
    border-width: 1px 0 0 1px;
    background: #fff;
    left: 0;
    right: 0;
    bottom: -32px;
    margin: 0 auto;
}

.progress-step-name.active {
    color: @green;
}

:host-context(.contrasto-on) {
    .progress {
        background-color: @white;
        border: 0.125em solid @black;
    }
    .nbp-progress-container {
        background-color: @white;
        border-bottom: 1px solid @black;
    }
    .progress.active {
        border: 0.125em solid @black;
    }
    .progress-bar {
        background-color: @black;
    }
    .progress-step-name {
        color: @black;
    }
    .progress-step-name.arrowStep:after {
        transform: rotate(45deg);
        content: " ";
        width: 15px;
        height: 15px;
        border: 0 solid @black;
        border-width: 1px 0 0 1px;
        background: #fff;
        left: 0;
        right: 0;
        bottom: -32px;
        margin: 0 auto;
    }
    .progress-step-name.active {
        color: @black;
    }
}


@media(max-width:767px) {
  
.progress-container{
    width:80%;
  }
}

:host-context(.contrasto-on) {

    .progress.active {
        border: 0.125em solid @black;
    }

    .progress-bar {
        background-color: @black;
    }

    .progress-step-name {
        color: @black;
    }

    .progress-step-name.active {
        color: @black;
    }

}
